import React, { Component } from 'react'
import axios from 'axios'
import {Link, Redirect} from 'react-router-dom'
import Img from '../assets/logo.png'

export default class Searchcustomers extends Component {
    constructor(props){
        super(props)
        this.state = {
            msg:'',
            err:'',
            msg2:'',
            err2:'',
            msg3:'',
            err3:'',
            search: '',
            search2:'',
            searchsuccess:'',
            employee: this.props.location.state.employee,
            admin: this.props.location.state.admin,
            success:'',
            service: '',
            date: '',
            price: '',
            customer: '',
            id: '',
            discount:'',
            phone: '',
            aprice:'',
            total:0,
            sdate:'',
            sdate1:'',
            number:'',
            department:'',
        }
        this.search = this.search.bind(this)
        this.changeSearch2 = this.changeSearch2.bind(this)
        this.data = []
        this.changeSearch = this.changeSearch.bind(this)
        this.submit = this.submit.bind(this)
        this.changeDate = this.changeDate.bind(this)
        this.changeDate1 = this.changeDate1.bind(this)
        this.searchRange = this.searchRange.bind(this)
        this.changeNumber = this.changeNumber.bind(this)
        this.changeDepartment = this.changeDepartment.bind(this)
    }

    changeSearch(event){
        this.setState({search: event.target.value})
      }

      search(e){
        var data = []
        e.preventDefault()
        this.setState({msg: '* Searching *', err: ''})
        axios.post('/searchcustomer', {search: this.state.search})
        .then((res) => {
          for(var i = 0; i < res.data.customer.length; i++){
              data.push(res.data.customer[i])
          }
          if(res.data.success === false){
            this.setState({err: 'No Customer Found ', msg: ''})
          }
          console.log(res.data.success)
          this.setState({searchsuccess: res.data.success})
      })
      this.data = data
      console.log(this.data)
      }

      changeDepartment(event){
        this.setState({department: event.target.value})
      }

      changeSearch2(event){
        this.setState({search2: event.target.value})
    }

    

    changeDate(event){
        this.setState({sdate: event.target.value})
    }

    changeDate1(event){
        this.setState({sdate1: event.target.value})
    }

    changeNumber(event){
        this.setState({number: event.target.value})
    }

      submit(e){
        e.preventDefault()
        this.setState({msg2: '* Searching *', err2: ''})
        var data = []
        // var url = 'https://tslposapi.herokuapp.com/search'
        // var url2 = 'http://localhost:5000/search'
        var url3 = '/search'
            axios.post(url3, {department: this.state.department, search: this.state.search2, sdate: this.state.sdate, number: this.state.number})
            .then((res) => {
                for(var i = 0; i < res.data.length; i++){
                    data.push(res.data[i])
                    this.customers.push(res.data[i])
                }
                if(res.data.success === false){
                    this.setState({err2: 'No Customer Found ', msg2: ''})
                  }
                console.log(res.data.customers)
                this.customers = res.data.customers
                this.setState({success: res.data.success, department:'', search2:'', sdate:'', number:'', })
                console.log(this.customers)
            })
            
        }

        searchRange(e){
            e.preventDefault()
            this.setState({msg3: '* Searching *', err3: ''})
            var data = []
            // var url = 'https://tslposapi.herokuapp.com/range'
            // var url2 = 'http://localhost:5000/range'
            var url3 = '/range'
                axios.post(url3, {sdate1: this.state.sdate1, sdate: this.state.sdate})
                .then((res) => {
                    for(var i = 0; i < res.data.length; i++){
                        data.push(res.data[i])
                        this.customers.push(res.data[i])
                    }
                    if(res.data.success === false){
                        this.setState({err3: 'No Customer Found ', msg3: ''})
                      }
                    console.log(res.data.customers)
                    this.customers = res.data.customers
                    this.setState({success: res.data.success})
                    console.log(this.customers)
                })
                
            }

    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/receipts',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>View Receipts</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/custom',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Custom Receipt</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className='nav-link' to='' onClick={this.logout}><button className='btn btn-danger' style={{borderRadius:'30px'}}>Log Out</button></Link>
                        </li>
                    </ul>

                </div>
            </nav>


                <div id='search' className='container' style={{paddingLeft:'25%', paddingRight:'25%'}}>
                          <h4>Search Customer</h4>
                          <span style={{color:'green'}}>{this.state.msg}</span><br/>
                          <span style={{color:'red'}}>{this.state.err}</span><br/><br/>
                        <form onSubmit={this.search}>
                                <input id='text' type = 'text'
                                placeholder = 'Search Customer'
                                onChange = {this.changeSearch}
                                value = {this.state.search}
                                className = 'form-control form-group'
                                />
                                <input 
                                type = 'submit'
                                className = 'btn btn-danger btn-block'
                                value = 'Search'
                                />
                        </form><br/><br/>
                    </div>


                    <div className='row'>
                      <div className='col-sm-12'>
                      <div id='search'>
                          <h4>Search Receipt</h4>
                          <span style={{color:'green'}}>{this.state.msg2}</span><br/>
                          <span style={{color:'red'}}>{this.state.err2}</span><br/><br/>
                        <form onSubmit={this.submit}>
                                Department:<br/>
                                <select onChange = {this.changeDepartment} name="session" id="session" style={{width:'100%'}} className = 'form-control form-group'>
                                    <option value="">-- Choose Department --</option>
                                    <option value="TslMoments">TslMoments</option>
                                    <option value="TslBrands">TslBrands</option>
                                    <option value="TslDigital">TslDigital</option>
                                    <option value="TslMedia">TslMedia</option>
                                </select>
                                Customer or Employee:<br/>
                                <input id='text' type = 'text'
                                placeholder = 'Search Customer or Employee'
                                onChange = {this.changeSearch2}
                                value = {this.state.search2}
                                className = 'form-control form-group'
                                />
                                Date:<br/>
                                <input id='text' type = 'date'
                                placeholder = 'Date'
                                onChange = {this.changeDate}
                                value = {this.state.sdate}
                                className = 'form-control form-group'
                                />
                                Phone Number:<br/>
                                <input id='text' type = 'number'
                                placeholder = 'Phone Number'
                                onChange = {this.changeNumber}
                                value = {this.state.number}
                                className = 'form-control form-group'
                                />
                                <input 
                                type = 'submit'
                                className = 'btn btn-danger btn-block'
                                value = 'Search'
                                />
                        </form><br/><br/>
                    </div>
                      </div>


                      <div className='col-sm-12'>
                      <div id='search'>
                          <h4>Search Range </h4>
                          <span style={{color:'green'}}>{this.state.msg3}</span><br/>
                          <span style={{color:'red'}}>{this.state.err3}</span><br/><br/>
                        <form onSubmit={this.searchRange}>
                            From:<br/>
                                <input id='text' type = 'date'
                                placeholder = 'Date'
                                onChange = {this.changeDate1}
                                value = {this.state.sdate1}
                                className = 'form-control form-group'
                                />
                                To:<br/>
                                <input id='text' type = 'date'
                                placeholder = 'Date'
                                onChange = {this.changeDate}
                                value = {this.state.sdate}
                                className = 'form-control form-group'
                                />
                                <input 
                                type = 'submit'
                                className = 'btn btn-danger btn-block'
                                value = 'Search'
                                />
                        </form><br/><br/>
                    </div>
                      </div>
                  </div>
                {
                    this.state.searchsuccess === true ? <Redirect to={{pathname:'/customer', customers: this.data, employee: this.props.location.state.employee, admin: this.state.admin}} /> : null
                }
                {
                    this.state.success === true ? <Redirect to={{pathname:'/search', customers: this.customers, employee: this.state.eprops, admin: this.state.admin}} /> : null
                }
            </div>
        )
    }
}
