import React, { Component } from 'react'
import Img from '../assets/logo.png'
import {Link} from 'react-router-dom'


export default class Print extends Component {
    constructor(props){
        super(props)
        var today = new Date(),
        time = today.getHours() + ':' + today.getMinutes() + ':' + today.getSeconds();
        this.state = {
            admin: this.props.location.state.admin
        }
        this.time = time
    }
    render() {
        return (
            <div className='receipt'> 
                <div className='receipt-div'>
                <div className='container'>
                    <img src={Img} alt='' className='receipt-img'/><br/>
                    Suite 25, comfort shoping plaza 1,<br/>
                    Etete Road,
                    G.R.A,
                    Benin City,<br/>
                    Edo State,
                    Nigeria.<br/>
                    +2348023541175<br/><br/>
                    <strong>Date:</strong> {this.props.location.state.date}<br/>
                    <strong>Employee:</strong> {this.props.location.state.employee}<br/>
                    <hr style={{border:'1px dashed black'}}></hr>
                </div><br/>
                <div className='container'>
                <strong>Receipt No:</strong> {this.props.location.state.id}<br/>
                    <strong>Name:</strong> {this.props.location.state.customer}<br/>
                    <strong>Phone Number:</strong> {this.props.location.state.phone}<br/>
                    <strong>Service:</strong> {this.props.location.state.session}<br/>
                    <strong>Department:</strong> {this.props.location.state.department}<br/>
                    <strong>Units:</strong> {this.props.location.state.copies}<br/>
                    <strong>Actual Price:</strong> {'₦'+this.props.location.state.aprice}<br/>
                    <strong>Discount:</strong> {this.props.location.state.discount + '%'}<br/>
                    <hr style={{border:'1px dashed black'}}></hr>
                    <div className='receipt-total'>
                        <h2>Total</h2><br/>
                        <strong> {' ₦'+this.props.location.state.price}</strong>
                    </div>
                </div><br/><br/>
                <div className='receipt-link'>
                    <h4><Link style={{color:'red'}} to={{
                          pathname: "/receipts",
                          state: {
                            employee: this.props.location.state.employee,
                            admin: this.state.admin
                          }
                        }}>*Return to Dashboard*</Link></h4>
                </div><br/>
                <button onClick={()=>window.print()}>Print</button>
            </div>
            </div>
        )
    }
}
