import React, { Component } from 'react'
import axios from 'axios'
import '../App.css'
import {Link} from 'react-router-dom'
import Img from '../assets/logo.png'
import ReactHTMLTableToExcel from 'react-html-table-to-excel'


export default class Receipts extends Component {
    constructor(props){
        super(props)
        this.state = {
            search: '',
            service: '',
            date: '',
            price: '',
            customer: '',
            id: '',
            success: '',
            discount:'',
            phone: '',
            employee:'',
            aprice:'',
            total:0,
            sdate:'',
            sdate1:'',
            number:'',
            department:'',
            eprops: this.props.location.state.employee,
            admin: this.props.location.state.admin
        }
        this.data = []
        this.customers = []
    }

    async componentDidMount(){
        console.log(this.props)
        var data = []
        // var url = 'https://tslposapi.herokuapp.com/receipts'
        // var url2 = 'http://localhost:5000/receipts'
        var url3 = '/receipts'
        axios.get(url3)
        .then((res) => {
            for(var i = 0; i < res.data.length; i++){
                data.push(res.data[i])
                this.setState({data: res.data[i], total: this.state.total + res.data[i].price})
            }
        })
        this.data = data
    }

    

        

    render(){
        return(
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                  <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/></a>
                  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                      <span className="navbar-toggler-icon"></span>
                  </button>

                  <div className="collapse navbar-collapse" id="navbarSupportedContent">
                      <ul className="navbar-nav ml-auto">
                          <li className="nav-item">
                             <Link className='nav-link' to={{
                          pathname: "/dashboard",
                          state: {
                            employee: this.state.eprops,
                            admin: this.state.admin
                          }
                        }} style={{color:'black'}}><strong>Dashboard</strong></Link>
                          </li>
                          <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/searchcustomers',
                                      state: {
                                          employee: this.state.eprops,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Search</strong></Link>
                        </li>
                      </ul>

                  </div>
              </nav>
              <div className='container' >
                  <h2 style={{textAlign:'center', marginTop:'10%'}}>Transactions </h2><br/>
                <table id='table' className='container'>
                    <tbody>
                    <tr>
                        <th>Date</th>
                        <th>Customer</th>
                        <th className='hide'>Phone Number</th>
                        <th>Service</th>
                        <th className='hide'>Units</th>
                        <th className='hide'>Employee</th>
                        <th className='hide'>Discount</th>
                        <th className='hide'>Department</th>
                        <th>Price Paid</th>
                        <th className='print'>Print</th>
                    </tr>
                {
                    this.data.map(dat => {
                        return(
                            <tr key={dat._id}>
                                <td>{dat.date}</td>
                                <td>{dat.customer}</td>
                                <td className='hide'>{dat.phone}</td>
                                <td>{dat.session}</td>
                                <td className='hide'>{dat.copies}</td>
                                <td className='hide'>{dat.employee}</td>
                                <td className='hide'>{dat.discount + '%'}</td>
                                <td className='hide'>{dat.department}</td>
                                <td>{'₦' + dat.price}</td>
                                <td className='print'><Link to={{
                                        pathname: '/print',
                                        state: {
                                            session: dat.session,
                                            date: dat.date,
                                            price: dat.price,
                                            customer: dat.customer,
                                            id: dat.receipt,
                                            discount: dat.discount,
                                            aprice: dat.aprice,
                                            employee: dat.employee,
                                            phone: dat.phone,
                                            copies: dat.copies,
                                            department: dat.department,
                                            admin: this.state.admin
                                        }
                                        }}><button className='btn btn-danger' id='print'>Print</button>
                                </Link></td>
                            </tr> 
                         ) 
                    }).reverse()
                }
                <tr>
                    <td><strong>Total</strong></td>
                    <td>{this.data.length}</td>
                    <td className='hide'></td>
                    <td></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td>{'₦' + this.state.total}</td>
                    <td><ReactHTMLTableToExcel
                    id="export"
                    className="btn btn-success btn-md"
                    table="table"
                    filename="Table"
                    sheet="tablexls"
                    buttonText="Export"/></td>
                </tr>
                </tbody>
                </table><br/><br/><br/>
                
            </div>
            </div>
            
        )
    }
}
