import React, { Component } from 'react'
import axios from 'axios'
import {Link} from 'react-router-dom'
import Img from '../assets/logo.png'

export default class Createadmin extends Component {
    constructor(props){
        super(props)
        this.state = {
            admin: this.props.location.state.admin,
            employee:this.props.location.state.employee,
            message:'',
            error:'',
            username:'',
            password:'',
            type:'',
            success:''
        }
        this.onChange = this.onChange.bind(this)
        this.submit = this.submit.bind(this)
    }

    onChange(e){
        this.setState({[e.target.name]: e.target.value})
    }

    submit(event){
        event.preventDefault()
        const details = {
          username: this.state.username,
          password: this.state.password,
          type: this.state.type
        }
        this.setState({message: '*Loading*', error: '', success:''})
        // var url = 'https://tslposapi.herokuapp.com/signup'
        // var url2 = 'http://localhost:5000/signup'
        var url3 = '/signup'
        axios.post(url3, details)
        .then(res => {
          console.log(res.data);
          this.setState({success: res.data.success})
          if(res.data.success === false){
            this.setState({message: '', error: 'Username Already Exists', success:''})
          }else if(res.data.success === true)
          this.setState({message: '', success: 'Admin Created successfully', error:''})
        }
        )
        .catch(error => console.log(error))
      } 

    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/receipts',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>View Receipts</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/searchcustomers',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Search</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/createadmin',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Create Admin</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/custom',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Custom Receipt</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className='nav-link' to='' onClick={this.logout}><button className='btn btn-danger' style={{borderRadius:'30px'}}>Log Out</button></Link>
                        </li>
                    </ul>

                </div>
            </nav>

                <div className='container' style={{marginTop: '10%'}}>
              <h2 style={{textAlign:'center'}}>Create Admin</h2>
              <div className='form-div'>
              <span style={{color:'green'}}>{this.state.message}</span>
              <span style={{color:'red'}}>{this.state.error}</span>
              <span style={{color:'green'}}>{this.state.success}</span>
                <form onSubmit={this.submit}>
                  Username:
                  <input required type = 'text'
                  placeholder = 'Username'
                  onChange = {this.onChange}
                  value = {this.state.username}
                  className = 'form-control form-group'
                  name = 'username'
                  />
                  Password:
                  <input required type = 'password'
                  placeholder = 'Password'
                  onChange = {this.onChange}
                  value = {this.state.password}
                  className = 'form-control form-group'
                  name = 'password'
                  />
                  Account Type:
                        <select required onChange = {this.onChange} name="type" id="session" style={{width:'100%'}} className = 'form-control form-group'>
                            <option value="">-- Choose Account Type --</option>
                              <option value="Super Admin">Super Admin</option>
                              <option value="Regular Admin">Regular Admin</option>
                        </select>
                  <input 
                  type = 'submit'
                  className = 'btn btn-danger btn-block'
                  value = 'submit'
                  />
                </form><br/>
              </div>
            </div>
            </div>
        )
    }
}
